import * as THREE from "three";
export class Utils {
    static genRandomId() {
        let random = "";
        for (let i = 0; i < 5; i++)
            random += Utils.getRandomString();
        return random;
    }
    static getRandomString() {
        return Math.random().toString(36).substring(8, 15);
    }
    static distance(object1, object2) {
        let difference = new THREE.Vector3();
        // Calculate difference between objects' positions.
        difference.copy(object1).sub(object2);
        return difference.length();
    }
}
Utils.UP = new THREE.Vector3(0, 0, 1);
Utils.LEFT = new THREE.Vector3(-1, 0, 0);
Utils.TOP = new THREE.Vector3(0, 1, 0);
Utils.RIGHT = new THREE.Vector3(1, 0, 0);
Utils.BOTTOM = new THREE.Vector3(0, -1, 0);
export class Mesh extends THREE.Mesh {
    constructor(geometry, material) {
        super(geometry, material);
        this.isPacman = false;
        this.isGhost = false;
        this.isWall = false;
        this.isDot = false;
    }
}
